import React from 'react'
import {connect} from 'react-redux'
import {
  Button,
  Checkbox,
  DateTimeRangePicker,
  Input,
  NumberInput,
  Select,
  TopTips
} from '@tencent/bere'
import moment from 'moment'
import SiteSelectMap from '@modules/rejudge_v3/components/SiteSelectMap'

import rejudgeCfg from '@config/rejudge'
import utils from '@modules/rejudge_v3/utils'

const {MODEL, compare_rtir_code_default_value} = rejudgeCfg

class ProductQueryCondition extends React.Component {

  render() {
    const {code, confidence_level, handling_time,ratio, compare_rtir_code} = this.state
    const { sfpp_id, task_id,getImageState } = this.props
    return (
      <div>
        <SiteSelectMap />
        <div className="dib mr-12 un-choose">
          <Input placeholder="Code" value={code}
                 onChange={(e) => this.selectChange(e.target.value, 'code')}/>
        </div>
        <div className="dib mr-12 un-choose wl-num">
          
          <NumberInput min={0} max={100} step={1} value={confidence_level.from}
                       onChange={(value) => this.minQuzhiChange(value)}/>
          <span className="m10"> </span>
          <NumberInput min={1} max={100} step={1} value={confidence_level.to}
                       onChange={(value) => this.maxQuzhiChange(value)}/>
        </div>
        <div className="dib mr-12 un-choose wl-num">
          <span></span>
          <NumberInput min={0} max={100} step={1} value={ratio}
                       onChange={(value) => this.selectChange(value, 'ratio')}/>
        </div>
        <div className="dib mr-12 un-choose">
          <DateTimeRangePicker
            placeholder=""
            value={{from: handling_time.from, to: handling_time.to}}
            ange={{min: handling_time.from, max: handling_time.to}}
            onChange={({from, to}) => this.handleDateChange(this, from ,to)}
          />
        </div>
        <div className="dib mr-12 un-choose">
          <Checkbox
            label="Code"
            value={compare_rtir_code_default_value}
            checked={compare_rtir_code}
            onChange={(checked, value) => this.selectChange(checked, 'compare_rtir_code')}/>
        </div>
        <div className="dib mr-12 un-choose">
          {
            getImageState ?  (
              <Button loading>
                </Button>
            ) : (
              <Button onClick={this.handleQuery}
                      disabled={!sfpp_id || !!task_id}>
                </Button>
            )
          }
        </div>
      {/*  <ul className="row-ul">
          <li>
            <div>
              <Input placeholder="Code" value={code}
                     onChange={(e) => this.selectChange(e.target.value, 'code')}/>
            </div>
          </li>
          <li>
            <span></span>
            <NumberInput min={0} max={100} step={1} value={confidence_level.from}
                         onChange={(value) => this.minQuzhiChange(value)}/>
          </li>
          <li>
            <span className="fs20">-</span>
          </li>
          <li>
            <NumberInput min={1} max={100} step={1} value={confidence_level.to}
                         onChange={(value) => this.maxQuzhiChange(value)}/>
          </li>
        </ul>
        <ul className="row-ul">
          <li>
            <span></span>
            <NumberInput min={0} max={100} step={1} value={ratio}
                         onChange={(value) => this.selectChange(value, 'ratio')}/>
          </li>
        </ul>
        <ul className="row-ul clearAFloat">
          <li>
             <div></div>
            <div>
              <DateTimeRangePicker
                placeholder=""
                value={{from: handling_time.from, to: handling_time.to}}
                ange={{min: handling_time.from, max: handling_time.to}}
                onChange={({from, to}) => this.handleDateChange(this, from ,to)}
              />
            </div>
          </li>
          <li>
            <Checkbox
              label="Code"
              value={compare_rtir_code_default_value}
              checked={compare_rtir_code}
              onChange={(checked, value) => this.selectChange(checked, 'compare_rtir_code')}/>
          </li>
          <li>
            <div>
              {
                getImageState ?  (
                  <Button loading>
                    </Button>
                ) : (
                  <Button onClick={this.handleQuery}
                          disabled={!sfpp_id || !!task_id}>
                    </Button>
                )
              }
            </div>
          </li>
        </ul>*/}
      </div>

    )
  }

  constructor() {
    super()
    this.state = {
      sfpp_id: '',
      code: '',
      compare_rtir_code: false,
      confidence_level: {
        from: 0,
        to: 100
      },
      handling_time: {
        from: moment(new Date(new Date().getTime() - 24*60*60*1000))
          .startOf('hour')
          .format('YYYY-MM-DD HH:mm:ss'),
        to: moment(new Date()).startOf('hour').format('YYYY-MM-DD HH:mm:ss')
      },
      ratio: 0
    }
  }
  componentDidMount() {
    let dispatch = this.props.dispatch

    dispatch({
      type: `${MODEL}/sampleUploadLog`,
      callback: (res) => {
        if (res.result) {
          this.getImage_getCode()
        }
      }
    })

  }
  handleQuery =() => {
    let dispatch = this.props.dispatch
    let { confidence_level, code, handling_time, ratio, compare_rtir_code } = this.state
    let queryMap = {
      confidence_level, code, handling_time, compare_rtir_code
    }
    utils.start_getImageLoad()
    dispatch({
      type: `${MODEL}/extract_start`,
      queryMap,
      ratio: ratio,
      callback: (res) => {
        if (res.result) {
          this.getImage_getCode()
        }
      }
    })
  }

  getImage_getCode = () => {
    let {dispatch} = this.props
    dispatch({
      type: `${MODEL}/rejudge_getImage`,
      callback: (res) => {
        if (!res.result) return
        dispatch({
          type: `${MODEL}/setImageInfo`,
          ...res
        })
        utils.queryCode(this);
      }
    })
  }


  minQuzhiChange = (value, key) => {
    console.log(value, key)
    if (value < this.state.confidence_level.to) {
      this.setState({
        confidence_level:{
          from: value,
          to: this.state.confidence_level.to
        }
      })
    }
  }

  maxQuzhiChange = (value, key) => {
    console.log(value, key)
    if (value > this.state.confidence_level.from) {
      this.setState({
        confidence_level:{
          from: this.state.confidence_level.from,
          to: value
        }
      })
    }
  }

  selectChange = (value, key) => {
    console.log(value, key)
    let obj = {}
    obj[key] = value
    this.setState({
      ...obj
    })
    // utils.handleSelectChange(this, value, key)
  }
  handleDateChange = (that, from, to) => {
    from = moment(from).startOf('hour').format('YYYY-MM-DD HH:mm:ss')
    to = moment(to).startOf('hour').format('YYYY-MM-DD HH:mm:ss')
    this.setState({
      handling_time: {
        from: from,
        to: to
      }
    })
  }


}

export default connect(({rejudge3}) => {
  return {
    sysActive: rejudge3.sysActive,
    rejudgeType: rejudge3.rejudgeType,
    getImageState: rejudge3.getImageState,
    sfpp_id: rejudge3.sfpp_id,
    task_id: rejudge3.task_id,
    startTask: rejudge3.startTask,
    imageInfo: rejudge3.imageInfo,
  }
})(ProductQueryCondition)
