import React from 'react'
import {connect} from 'react-redux'
import {Link, withRouter} from 'react-router-dom'
import {RadioButtonGroup,} from '@tencent/bere'
import classnames from 'classnames'

import rejudgeCfg from '@config/rejudge'
import utils from '@modules/rejudge_v3/utils'
import Container from '@modules/rejudge_v3/components/container'
import ProductQueryCondition from '@modules/rejudge_v3/spotCheckSys/components/ProductQueryCondition'
import FileQueryCondition from '@modules/rejudge_v3/spotCheckSys/components/FileQueryCondition'



const MODEL = rejudgeCfg.MODEL;
const SYSTEM = 'spotCheckSys';

import MainLayout from '@layouts/MainLayout'
import PanelLayout from '@layouts/PanelLayout'

const queryList = {
  product: {content: <ProductQueryCondition />},
  file: {content: <FileQueryCondition />},
}

const rejudgeTypes = rejudgeCfg.rejudgeType.spotCheckSys

class App extends React.Component{

  constructor(props) {
    super(props)
    this.state = {
      panelIsShow: false
    }
  }
  _handlePandleClick = () => {
    this.setState(preState => ({
        panelIsShow: !preState.panelIsShow
      })
    )
  }

  render() {
    const {sysActive, rejudgeType} = this.props
    const { panelIsShow } = this.state
    return (
      <MainLayout className="large-page sample">
        <PanelLayout className={classnames({'panel-hide': panelIsShow})} collapse={
          <div className={classnames({ "can-hide": !panelIsShow }, { "can-show": panelIsShow })} onClick={e => this._handlePandleClick()}></div>
        }>
          <div className='mb-24'>
            <RadioButtonGroup
              value={sysActive}
              list={rejudgeCfg.sysMenus}
              onChange={(val) => utils.switchSystem(this, val)}
            />
            <RadioButtonGroup
              value={rejudgeType}
              list={rejudgeTypes}
              onChange={(val) => utils.switchRejudgeType(this, val)}
            />
          </div>
          {queryList[rejudgeType].content}
        </PanelLayout>
        <Container />
        {/*  <div className="rejudge main adc-manage-main">
        <div className="manage-area">
          <div className="top-title-box">
            <ul className="row-ul clearAFloat">
              <li style={{marginRight: '80px'}}>
                <RadioButtonGroup
                  value={sysActive}
                  list={rejudgeCfg.sysMenus}
                  onChange={(val) => utils.switchSystem(this, val)}
                />
              </li>
              <li>
                <div>
                  <RadioButtonGroup
                    value={rejudgeType}
                    list={rejudgeTypes}
                    onChange={(val) => utils.switchRejudgeType(this, val)}
                  />
                </div>
              </li>
            </ul>
            <div>
              {queryList[rejudgeType].content}
            </div>

          </div>
          <Container/>
        </div>
      </div>*/}
      </MainLayout>
    )
  }

  keyboardEvent = (e) => {
    utils.keyboardEvent(e, this)
  }

  componentWillMount() {
    utils.localSystem(this)
    this.props.dispatch({
      type: `${MODEL}/requestCodeType`
    })
  }

  componentDidMount(){
    document.addEventListener("keydown", this.keyboardEvent, false);
  }
  componentWillUnmount(){
    document.removeEventListener("keydown", this.keyboardEvent, false);
  }
}

const sagaMap = ({ rejudge3 }) => {
  return {
    startTask: rejudge3.startTask,
    uploadState: rejudge3.uploadState,
    getImageState: rejudge3.getImageState,
    num_results: rejudge3.num_results,
    imageInfo: rejudge3.imageInfo,
    sysActive: rejudge3.sysActive,
    rejudgeType: rejudge3.rejudgeType,
    codeBtnActiveNumber: rejudge3.codeBtnActiveNumber,
    hoverCode: rejudge3.hoverCode,
    image_area_code_arr: rejudge3.image_area_code_arr,
    imageCurrentIndex: rejudge3.imageCurrentIndex,
    direction_index: rejudge3.direction_index,
    focusCodeInput: rejudge3.focusCodeInput,
    detailFullScrollState: rejudge3.detailFullScrollState,
  }
}

const connectSaga = connect(sagaMap)(App)

export default withRouter(connectSaga)
