import React from 'react'
import classnames from 'classnames'

const MainLayout = ({ full = true, title, right, windowHeight, secondary, secondaryLink, children, className }) => <div className={classnames("main", className)} style={{ minHeight: windowHeight }}>
  <div className="manage-area">
    {
      (title || right) && <div className={classnames("manage-area-title", {['secondary-title']: secondary})}>
        {
          secondary && <>
            <a href={secondaryLink} class="back-link"><i class="btn-back-icon"></i></a>
            <span class="line-icon">|</span>
          </>
        }
        <h2>{title}</h2>
        {
          right && <div className="manage-area-title-right">{right}</div>
        }
      </div>
    }
    <div className={classnames("manage-area-main", {['secondary-main']: secondary})}>
      {
        full ? <div className="manage-area-full-inner">{children}</div> : children
      }</div>
  </div>
</div>

export default MainLayout
