import React from 'react'
import {PageLoading} from '@tencent/bere'
import {connect} from 'react-redux'
import DetailPage from '@modules/rejudge_v3/components/DetailPage'
import Panel from '@layouts/PanelLayout.js'

// import '@modules/rejudge_v3/lib/jquery.ba-resize.min'


class Container extends React.Component {
  render() {
    let {windowHeight } = this.props
    const mainModuleHeight = windowHeight - 80
    return (
      <div className="main-area" style={{height: `${mainModuleHeight}px`, position: 'relative'}}>
        <div className="detail-loading" id="imageDetailLoad">
          <PageLoading/>
        </div>
        <DetailPage/>
      </div>
    )
  }

  constructor() {
    super()
  }

  componentDidMount() {

  }

}


const connectProps = ({rejudge3, app}) => {
  return {
    windowHeight: app.windowHeight,
    detailFullScrollState: rejudge3.detailFullScrollState
  }
}

export default connect(connectProps)(Container)

