import React from 'react'
import {connect} from 'react-redux'
import {DropdownList, Checkbox, DateTimeRangePicker, Input, Select, TopTips} from '@tencent/bere'
import rejudgeCfg from '@config/rejudge'

const MODEL = rejudgeCfg.MODEL

const ascSort = (a, b) => (a > b ? 1 : -1)
const descSort = (a, b) => (b > a ? 1 : -1)


class SiteSelectMap extends React.Component {

  render() {
    const {site, shop, operation, product,} = this.state
    const {siteMap, productList, task_id } = this.props

    let buttonProps = {
      style: {
        minWidth: 100,
        maxWidth: 250
      }
    }
    let menuProps = {
      style: {
        height: 220
      }
    }

    let curSite;
    let shopList;
    let operationList;
    const siteList = siteMap
      .map(item => {
        if (item.site === site) {
          curSite = item
        }
        return { text: item.site, value: item.site }
      })
      .sort((a, b) => ascSort(a.text, b.text))
    if (curSite && curSite.factory_list) {
      let curShop
      shopList = curSite.factory_list
        .map(item => {
          if (item.factory_name === shop) {
            curShop = item
          }
          return { text: item.factory_name, value: item.factory_name }
        })
        .sort((a, b) => ascSort(a.text, b.text))

      if (curShop && curShop.pon_list) {
        operationList = curShop.pon_list
          .map(item => ({
            text: item.process_operation_name,
            value: item.process_operation_name
          }))
          .sort((a, b) => ascSort(a.text, b.text))
      }
    }
    return (
      <>
        <div className="dib mr-12 un-choose">
          {/*  */}
          <DropdownList
            value={site} list={siteList}
            type="button"
            buttonProps={buttonProps}
            menuProps={menuProps}
            placeholder=""
            onChange={(value) => this.selectChange(value, 'site')}
          />
        </div>
        <div className="dib mr-12 un-choose">
          {/*  */}
          <DropdownList
            value={shop} list={shopList}
            type="button"
            buttonProps={buttonProps}
            menuProps={menuProps}
            placeholder=""
            onChange={(value) => this.selectChange(value, 'shop')}
          />
        </div>
        <div className="dib mr-12 un-choose">
          {/*  */}
          <DropdownList
            value={operation} list={operationList}
            type="button"
            buttonProps={buttonProps}
            menuProps={menuProps}
            placeholder=""
            onChange={(value) => this.selectChange(value, 'operation')}
          />
        </div>
        <div className="dib mr-12 un-choose">
          {/*  */}
          <DropdownList
            value={product} list={productList}
            type="button"
            buttonProps={buttonProps}
            menuProps={menuProps}
            placeholder=""
            onChange={this.setSFPP_ID}
          />
        </div>
      {/*  <ul className="row-ul">
          <li>
            <div>
              <Select className="mW100 tc-15-select" placeholder=""
                      disabled={task_id}
                      value={site} list={siteList}
                      onChange={(value) => this.selectChange(value, 'site')}>
              </Select>
            </div>
          </li>
          <li>
            <div>
              <Select className="mW100 tc-15-select" placeholder="" disabled={!site}
                      value={shop} list={shopList}
                      onChange={(value) => this.selectChange(value, 'shop')}>
              </Select>
            </div>
          </li>
          <li>
            <div>
              <Select className="mW150 tc-15-select" placeholder="" disabled={!shop}
                      value={operation} list={operationList}
                      onChange={(value) => this.selectChange(value, 'operation')}>
              </Select>
            </div>
          </li>
          <li>
            <div>
              <Select className="mW150 tc-15-select" placeholder="" disabled={!operation}
                      value={product} list={productList}
                      onChange={this.setSFPP_ID}>
              </Select>
            </div>
          </li>
        </ul>*/}

      </>

    )
  }

  constructor() {
    super()
    this.state = {
      site: '',
      shop: '',
      operation: '',
      product: ''
    }
  }
  componentDidMount() {

    this.props.dispatch({
      type: `${MODEL}/requestSite`
    })
  }


  selectChange = (value, key) => {
    console.log(value, key)
    let obj = {};
    let _value = typeof value === 'object' ? value.value: value
    obj[key] = _value
    if (key === 'site') {
      obj.shop = '';
      obj.operation = '';
      obj.product = '';
    }

    this.setState({
      ...obj
    })
    if (key === 'operation') {
      let {site, shop} = this.state
      this.props.dispatch({
        type: `${MODEL}/getProductList`,
        data: {
          site,
          shop,
          operation: _value
        }
      })
    }
  }

  setSFPP_ID = (value) => {
    let _value = typeof value === 'object' ? value.value: value
    this.props.dispatch({
      type: `${MODEL}/changBaseState`,
      data: {
        sfpp_id:  _value
      }
    })
    this.setState({
      product:  _value
    })
  }




}

export default connect(({rejudge3}) => {
  return {
    task_id: rejudge3.task_id,
    sysActive: rejudge3.sysActive,
    rejudgeType: rejudge3.rejudgeType,
    siteMap: rejudge3.siteMap,
    productList: rejudge3.productList
  }
})(SiteSelectMap)
