import React from 'react'
import {connect} from 'react-redux'
import {Button, Checkbox, DateRangePicker, Input, NumberInput, Select, TopTips} from '@tencent/bere'

import rejudgeCfg from '@config/rejudge'
import utils from '@modules/rejudge_v3/utils'
import Icon from '@components/Icon'

const {MODEL, templateFileUrl, compare_rtir_code_default_value} = rejudgeCfg

class FileQueryCondition extends React.Component {

  render() {
    const { upload_sample_name, task_id, startTask, uploadState,
      getImageState
    } = this.props
    const {code, confidence_level, compare_rtir_code} = this.state
    return (
      <>
        {!upload_sample_name ? (
          <div className="form-upload mr-12">
            <div href="javascript:void(0);" className="tc-upload-btn" style={{overflow: 'initial'}} title="">
              <input type="text" className="tc-15-input-text m" readOnly="true" placeholder="" />
              <div className="file-input-wrap">
                <input type="file" title="csv" id="uploadRejudgeFile"
                       onChange={this.upload}/>
              </div>
              <a className="mr15 upload-icon-a" target="_blank" href={templateFileUrl}
              >
                <div className="title-tip">
                  
                </div>
                <Icon type="infomation" >

                </Icon>
              </a>
            </div>
          </div>
        ): (
          <input type="text" className="tc-15-input-text m" readOnly="true" placeholder={upload_sample_name} />
        )}
        <div className="dib mr-12 un-choose wl-num">
          
          <NumberInput min={0} max={100} step={1} value={confidence_level.from}
                       onChange={(value) => this.minQuzhiChange(value)}/>
          <span className="m10"> </span>
          <NumberInput min={1} max={100} step={1} value={confidence_level.to}
                       onChange={(value) => this.maxQuzhiChange(value)}/>
        </div>
        <div className="dib mr-12 un-choose">
          <Input placeholder="Code" value={code}
                 onChange={(e) => this.selectChange(e.target.value, 'code')}/>
        </div>
        <div className="dib mr-12 un-choose">
          <Checkbox
            label="Code"
            value={compare_rtir_code_default_value}
            checked={compare_rtir_code}
            onChange={(checked, value) => this.selectChange(checked, 'compare_rtir_code')}/>
        </div>
        <div className="dib mr-12 un-choose">
          {
            getImageState ?  (
              <Button loading>
                </Button>
            ) : (
              <Button onClick={this.handleQuery}
                      disabled={!task_id || startTask}>
                </Button>
            )
          }
        </div>
        {/*{!upload_sample_name ? (
        <ul className="row-ul">
          <li>
            {uploadState ?
              (
                <span className={'upload_loadingGif'}
                      title="">
                    </span>
              ): (
                <span>
                  <label className="queryUploadBox" htmlFor="uploadRejudgeFile"
                         title={``}>
                          CSV
                  </label>
                  <input type="file" id="uploadRejudgeFile" style={{display: 'none'}}
                      onChange={this.upload}/>
                </span>
              )
            }
          </li>
          <li>
            <a className="mr15" target="_blank" href={templateFileUrl}>
              
            </a>
          </li>
        </ul>
      ): (
        <ul className="row-ul">
          <li><div className="uploadFileName">{upload_sample_name}</div></li>
        </ul>
      )}
        <ul className="row-ul">
          <li>
            
            <NumberInput min={0} max={100} step={1} value={confidence_level.from}
                         onChange={(value) => this.minQuzhiChange(value)}/>
          </li>
          <li>
            <span className="fs20">-</span>
          </li>
          <li>
            <NumberInput min={1} max={100} step={1} value={confidence_level.to}
                         onChange={(value) => this.maxQuzhiChange(value)}/>
          </li>
        </ul>

        <ul className="row-ul">
          <li>
            <div>
              <Input placeholder="Code" value={code}
                     onChange={(e) => this.selectChange(e.target.value, 'code')}/>
            </div>
          </li>
          <li className="pl35">
            <Checkbox
              label="Code"
              value={compare_rtir_code_default_value}
              checked={compare_rtir_code}
              onChange={(checked, value) => this.selectChange(checked, 'compare_rtir_code')}/>
          </li>
        </ul>
        <ul className="row-ul clearAFloat">
          <li>
            <div>
              {
                getImageState ?  (
                  <Button loading>
                    </Button>
                ) : (
                  <Button onClick={this.handleQuery}
                          disabled={!task_id || startTask}>
                    </Button>
                )
              }
            </div>
          </li>
        </ul>*/}
      </>

    )
  }

  constructor() {
    super()
    this.state = {
      sfpp_id: 0,
      code: '',
      compare_rtir_code: false,
      confidence_level: {
        from: 0,
        to: 100
      },
    }
  }

  componentWillMount() {

  }
  componentDidMount() {
    let dispatch = this.props.dispatch

    // dispatch({
    //   type: `${MODEL}/sampleUploadLog`,
    //   callback: (res) => {
    //     if (res.result) {
    //
    //     }
    //   }
    // })
  }

  handleQuery =() => {
    // utils.handleQuery(this)
    let dispatch = this.props.dispatch
    let { confidence_level, code, sfpp_id,compare_rtir_code } = this.state
    let queryMap = {confidence_level, code, compare_rtir_code}
    utils.start_getImageLoad()
    dispatch({
      type: `${MODEL}/handleQueryImage`,
      queryMap,
      callback: (res) => {
        if (res.result) {
          dispatch({
            type: `${MODEL}/setImageInfo`,
            ...res
          })
          utils.queryCode(this);
        }
      }
    })
  }

  minQuzhiChange = (value, key) => {
    console.log(value, key)
    if (value < this.state.confidence_level.to) {
      this.setState({
        confidence_level:{
          from: value,
          to: this.state.confidence_level.to
        }
      })
    }
  }

  maxQuzhiChange = (value, key) => {
    console.log(value, key)
    if (value > this.state.confidence_level.from) {
      this.setState({
        confidence_level:{
          from: this.state.confidence_level.from,
          to: value
        }
      })
    }
  }


  // 
  upload = (e) => {
    const file = e.target.files[0]
    if (!file) return
    this.props.dispatch({
      type: `${MODEL}/requestSampleUpload`,
      file: file,
      callback: (res) => {
        // console.log(res)
      }
    })
    e.target.value = ''
  }
  selectChange = (value, key) => {
    console.log(value, key)
    let obj = {}
    obj[key] = value
    this.setState({
      ...obj
    })
    // utils.handleSelectChange(this, value, key)345435435435
  }

}

export default connect(({rejudge3}) => {
  return {
    startTask: rejudge3.startTask,
    uploadState: rejudge3.uploadState,
    getImageState: rejudge3.getImageState,
    task_id: rejudge3.task_id,
    sysActive: rejudge3.sysActive,
    rejudgeType: rejudge3.rejudgeType,
    upload_sample_name: rejudge3.upload_sample_name,
    imageInfo: rejudge3.imageInfo,
  }
})(FileQueryCondition)
