import React from 'react'
import {connect} from 'react-redux'
import {withRouter} from 'react-router-dom'
import {Button, Checkbox, Dialog, PageLoading, RadioGroup, SearchBox, TopTips, Input} from '@tencent/bere'
import utils from '@modules/rejudge_v3/utils'
import rejudgePreview from '../../../static/img/rejudgePreview.png'

import rejudgeCfg from '@config/rejudge'

import GridLayout from '@layouts/GridLayout';
import GridCellLayout from '@layouts/GridCellLayout';
import Icon from '@components/Icon'

const {MODEL, codeId, testImageUrl, codeBtnRowNumber} = rejudgeCfg

class DetailPage extends React.Component {

  render() {
    const {img_url, id, rtir_code,rework, turn_on, sheet_type, adc_code} = this.props.imageInfo
    const {
      num_results,importCodeState,detailFullScrollState,
      imageCurrentIndex, sysActive, imageCodeList,
      windowHeight
    }
    = this.props
    const { sheetValue, searchList_active, searchText } = this.state
    let sysEndText = sysActive === 'sampleSys' ? '': '';
    const mainModuleHeight = windowHeight - 80
    const imageHeight = mainModuleHeight - 180
    return (
      <>
        {img_url ? (
          <>
              <div className="main-area-left" style={{width: `${imageHeight / 3 * 4}px`}}>
                <div className="title" style={{position: 'relative'}}>
                   <em>{num_results - (imageCurrentIndex + 1)}</em> 
                  <span className="tip"> <em>{num_results}</em> </span>

                  {sysActive !== 'sampleSys' ? (
                    <div className="adc-title">
                      <span className="mr20">
                        ADCCode<span className="value">{adc_code}</span>
                      </span>
                      <span className="mr20">
                        RTIRCode<span className="value">{rtir_code}</span>
                      </span>
                    </div>
                  ): (
                    ''
                  )}

                  <div className="swiper-area" style={{height: `${imageHeight}px`}}>
                    <div id="imageBox" className="imgBox">
                      <div>
                        <div id="imageLoad">
                          <PageLoading/>
                        </div>
                        <img id="image" src={testImageUrl + img_url}
                             style={{width: '100%', cursor: 'pointer', display: 'none'}}/>
                      </div>
                      <div id="imageError" style={{ display: 'none', color: 'rgba(150,150,150,1)'}}>
                        
                      </div>
                      <div className="previousImage" title="F5"
                           onClick={() => this.switchImg('previousImg')}>
                        <div className="box-middle"> {'<'} </div>
                      </div>
                      <div className="nextImage" title="F6"
                           onClick={() => this.switchImg('nextImg')}>
                        <div className="box-middle"> {'>'} </div>
                      </div>

                    </div>
                  </div>
                </div>

              </div>
            <div className="main-area-right">
              <div className="head">
                <div onKeyDown={this.searchOrAddCodeKeyFunc} style={{position: 'relative'}}>
                  <SearchBox placeholder="Code" className="wl-icon-add"
                             rel="searchOrAddCode"
                             id="searchOrAddCode"
                             onClear={()=> this.setState({ searchText: '' })}
                             onChange={this.codeListenSearchText}
                             onSearch={this.addCode}
                             onBlur={()=>{setTimeout(()=>this.showSearchlist(false),200)}}
                             onFocus={()=>{this.showSearchlist(true)}}
                             value={searchText}
                  />
                  <div className="tc-15-autocomplete" style={{"width":"calc(100% - 181px)"}} >
                    {this.state.show_searchlist?
                      <ul className="tc-15-autocomplete-menu" role="menu" style={{"marginTop":0,"display":"block"}}>
                        {
                          this.state.showList.length>0?
                            this.state.showList.map((item) => (
                              <li role="presentation" key={item.code}
                                  className={searchList_active === item.code ? 'active' : ''}>
                                <a className="text-truncate" role="menuitem" href="javascript:;"
                                   onClick={() => this.chooseCodeBtn(item)}>{item.code}</a>
                              </li>
                            ))
                            :(this.state.searchText?<li role="presentation">
                              <a className="autocomplete-empty" role="menuitem" href="javascript:;">
                                {searchText ? <span>code <strong>{searchText}</strong></span>
                                  : ``}
                              </a>
                            </li>:"")
                        }
                      </ul>
                      :""
                    }
                  </div>
                  <span className="ml-10 importCodeBox">
                          {importCodeState ? (
                            <span className={'upload_loadingGif'} title="code">
                            </span>
                          ) : (
                            <span>
                              <input id="importCode" type="file" style={{display: 'none'}}
                                     onChange={this.imageImportCode}/>
                              <label htmlFor="importCode" className="label-button-default" title="Code">
                                <span className="icon-import"></span>
                                <span className="icon-text">Code</span>
                              </label>
                            </span>
                          )}
                     </span>
                </div>
              </div>
              <div className="operating-area">
                <div className="choose-code-area codeBtn-box">
                  <GridLayout>
                    {this.setKeyboardBtn(imageCodeList)}
                  </GridLayout>
                  <div className="choose-code-area-empty">
                    <span className="choose-code-area-empty-inner"> CODE <strong>ESDAB</strong></span>
                  </div>
                </div>
                <div className="clearfix" style={{ height: '30px', lineHeight: '30px' }}>
                  <div className="pull-left"></div>
                  <div className="pull-right tip-text">{` ${imageCodeList.length}  CODE`}</div>
                </div>
                <div style={{ height: '114px' }}>
                  <GridLayout className="button-group">
                    <GridCellLayout colspan={12}>
                      <Button className={`imageInfo-btn ${rework ? 'active' : ''}`} style={{width: '100%'}}
                              onClick={this.rework}>
                        {rework ? <strong className="mr5"></strong> : ''}REWORK
                      </Button>
                    </GridCellLayout>
                    <GridCellLayout colspan={12}>
                      <Button className={`imageInfo-btn ${turn_on ? 'active' : ''}`} style={{width: '100%'}}
                              onClick={this.turnOn}>
                        {turn_on ? <strong className="mr5"></strong> : ''}TURN ON
                      </Button>
                    </GridCellLayout>
                    <GridCellLayout colspan={12}>
                      <Button className={`imageInfo-btn weak ${sheet_type ? 'active' : ''}`} style={{width: '100%'}}
                              onClick={this.abnormal}
                      >
                        <Icon className="mr-8" type="abnormal" />
                        {sheet_type ? <span><strong className="mr5"></strong></span> : ''}
                        
                      </Button>
                    </GridCellLayout>
                    <GridCellLayout colspan={12}>
                      <Button className="imageInfo-btn weak" style={{width: '100%'}}
                              onClick={this.endRejudge}>
                        <Icon className="mr-8" type="finish" />
                        {sysEndText}
                      </Button>
                    </GridCellLayout>
                  </GridLayout>
                </div>
              </div>
            </div>
          </>
        ) : (
          <div>
            <img src={rejudgePreview} style={{width: '100%'}}/>
          </div>
        )}
        {/*  */}
        <Dialog title={`${sysEndText}`} visible={this.state.endModal.show} okText={`${sysEndText}`}
                cancelText={`${sysEndText}`}
            /*    onOk={() => {this.endTaskBtn()}}*/
                onCancel={() => {this.endModalState(false)}}
                footerRender={this.endModalFooterRender}
        >
          <div>
            <p>
              {sysEndText}{sysEndText}
              {sysEndText}
            </p>
            <p>{sysEndText}</p>
          </div>
        </Dialog>

        {/*  */}
        <Dialog title="" visible={this.state.sheetModal.show} okText="" cancelText=""
                onOk={() => {
                  this.handleSheetType();
                  this.handleSheetModal(false);
                 }}
                onCancel={() => {this.handleSheetModal(false)}}
        >
          <div><span></span>
          </div>
          <div>
            <RadioGroup
              value={sheetValue}
              list={this.state.sheetList}
              onChange={(value) => {
                this.setState({
                  sheetValue: value
                })
              }}/>
          </div>
        </Dialog>

      </>
    )
  }

  constructor(props) {
    super(props)
    let {sheet_type} = props
    this.state = {
      searchText: '',
      rejudgeName: 3,
      endModal: {
        show: false,
        loading: false
      },
      sheetModal: {
        show: false
      },
      sheetValue: sheet_type ? sheet_type : 'A',
      sheetList: [
        {title: 'A', text: 'A', value: 'A'},
        {title: 'B', text: 'B', value: 'B'},
        {title: 'H', text: 'H', value: 'H'},
        {title: 'S', text: 'S', value: 'S'},
        {title: 'E', text: 'E', value: 'E'}
      ],
      showList:[],
      show_searchlist: false,
      searchList_index: -1,
      searchList_active: '',
    }
  }

  imageImportCode = (e) => {
    const file = e.target.files[0]
    if (!file) return;
    this.props.dispatch({
      type: `${MODEL}/imageImportCode`,
      file: file,
      callback: (res) => {
        if (res.result) {
          this.props.dispatch({
            type: `${MODEL}/queryCode`,
            callback: (res) => {
              if (res.result) {}
            }
          })
        }
        console.log(res)
      }
    })
    e.target.value = ''
  }

  addCode = (val) => {
    let {showList} = this.state
    let {dispatch} = this.props
    if(!val) return TopTips({theme: 'error', message: 'Code'})

    let existShowList = []

    if (showList.length) {
      existShowList = showList.filter(item => {
        return item.code === val
      })
      if (existShowList.length) {
        dispatch({
          type: `${MODEL}/chooseCodeBtn`,
          data: {
            codeBtnActiveNumber: val,
            valid_code: val,
          }
        })
        this.setState({
          show_searchlist: false
        })
        return
      }
    }

    dispatch({
      type: `${MODEL}/addCode`,
      data: {
        code: val
      },
      callback: (res) => {
        if (res.result) {
          dispatch({
            type: `${MODEL}/queryCode`,
            callback: (res) => {
              if (res.result) {
                // let { imageCodeList } = res.data
                dispatch({
                  type: `${MODEL}/chooseCodeBtn`,
                  data: {
                    codeBtnActiveNumber: val,
                    valid_code: val
                  }
                })
              }
            }
          })
        }
      }
    })
  }

  // code
  codeListenSearchText = (val) => {
    let showList = [];
    const {imageCodeList} = this.props
    showList = imageCodeList.filter(function(item){
      return item.code.toLowerCase().startsWith(val.toLowerCase())
    })
    this.setState({
      searchText: val,
      showList:showList,
      searchList_index: -1,
      searchList_active: '',
    })
  }

  showSearchlist = (boole) => {
    this.setState({
      show_searchlist: boole
    })
    this.props.dispatch({
      type: `${MODEL}/changBaseState`,
      data: {
        focusCodeInput: boole
      }
    })
    if (!boole) {
      this.setState({
        searchList_index: -1,
        searchList_active: '',
      })
    }
  }

  /*** */
  switchImg = (type) => {
    utils.handleSwitchImg(this, type, this.props.codeBtnActiveNumber)
  }

  /**rework * */
  rework = () => {
    utils.rework(this);
  }
  /**turnOn * */
  turnOn = () => {
    utils.turnOn(this);
  }

  /** * */
  abnormal = () => {
    let {codeBtnActiveNumber} = this.props
    // code
    if (!codeBtnActiveNumber) {
      TopTips({
        theme: 'warning',
        message: 'code',
        duration: 3000
      })
      return
    }
    this.setState({
      sheetModal: {
        show: true
      }
    })
  }
  handleSheetType = () => {
    this.props.dispatch({
      type: `${MODEL}/r_t_s`,
      data: {
        sheet_type: this.state.sheetValue
      }
    })
  }
  // 
  handleSheetModal = () => {
    this.setState({
      sheetModal: {
        show: false
      }
    })
  }

  /**code* */
  chooseCodeBtn = (item) => {
    console.log('item', item)
    let { codeBtnActiveNumber } = this.props
    if(codeBtnActiveNumber == item.code) return

    this.props.dispatch({
      type: `${MODEL}/chooseCodeBtn`,
      data: {
        codeBtnActiveNumber: item.code,
        valid_code: item.code
      }
    })
  }

  /*** */
  endModalFooterRender = () => {
    let {sysActive} = this.props
    let {loading} = this.state.endModal
    let sysEndText = sysActive === 'sampleSys' ? '': '';
    return (
      <div className="text-right modal-footerBtn">
        {
          loading ?
            (<Button loading>{sysEndText}</Button>)
            : ( <Button onClick={() => {this.endTaskBtn()}}>{sysEndText}</Button>)
        }
        <Button onClick={() => {this.endModalState(false)}}>{sysEndText}</Button>
      </div>
    );
  }
  endRejudge = () => {
    this.setState({
      endModal: {
        show: true
      }
    })
  }
  endTaskBtn = () => {
    let {endModal} = this.state
    this.setState({
      endModal:{
        ...endModal,
        loading: true
      }
    })
    this.props.dispatch({
      type:`${MODEL}/endTask`,
      callback: (res) => {

        this.setState({
          endModal:{
            ...endModal,
            loading: false
          }
        })

        if (res.result) {
          this.jumpViewResult()
        }
      }
    })
  }
  endModalState = (status) => {
    this.setState({
      endModal: {
        show: status
      }
    })
  }


  /*** */
  jumpViewResult = () => {
    this.props.history.push('/viewResult')
  }

  /**code* */
  setCodeBtns = (arr) => {
    let _this = this
    return (
      <div>
        {arr.map((item, index) => {
            return (
              <Button className={_this.props.codeBtnActiveNumber === item.code ? 'active' : ''}
                      onClick={() => this.chooseCodeBtn(item)}
                      key={index}  id={codeId + item.code}>
                {
                  _this.props.codeBtnActiveNumber === item.code ?
                    <strong className="mr5"></strong> : ''
                }
                {item.code}
              </Button>
            )
          })
        }

      </div>
    )
  }
  setKeyboardBtn = (arr) => {
    return this.splitDOM(arr, codeBtnRowNumber)
  }
  splitDOM = (arr, num) => {
    let _this = this
    let _arr = [...arr]
    let _arrSession = []
    let _row = []
    let _rowDiv = []
    let _btnNumber = 0
    for (let i = 0; i < _arr.length; i += num) {
      _arrSession.push(_arr.slice(i, i + num))
    }
    for (let i = 0; i < _arrSession.length; i++) {
      _row = _arrSession[i].map((item, index) => {
        _arr[_btnNumber].index = _btnNumber
        let dom =
          <Button className={`${_this.props.codeBtnActiveNumber === item.code ? 'active' : ''}`}
                  onClick={() => this.chooseCodeBtn(item)}
                  key={_btnNumber} title={item.code} id={codeId + item.code}>
            {
              _this.props.codeBtnActiveNumber === item.code ?
                <strong className="mr5"></strong> : ''
            }
            {item.code}
          </Button>
        _btnNumber++
        return dom
      })
      _rowDiv[i] = <div key={'spasd' + i}>{[..._row]}</div>
    }
    // console.log('_arr', _arr)
    this.props.dispatch({
      type: `${MODEL}/changBaseState`,
      data: {
        image_area_code_arr: _arr
      }
    })
    return _rowDiv
  }

  searchOrAddCodeKeyFunc = (e) => {
    // console.log('searchOrAddCode', e.keyCode)
    let { searchList_index, showList } = this.state
    let currentIndex;
    let currentCode;
    switch(e.keyCode) {
      case 38:
        e.preventDefault();
        currentIndex = searchList_index - 1;
        if (!showList[currentIndex]) return
        currentCode = showList[currentIndex].code;

        this.setState({
          searchList_index: currentIndex,
          searchList_active: currentCode,
          searchText: currentCode
        })
        break;
      case 40:
        e.preventDefault();
        currentIndex = searchList_index + 1
        if (!showList[currentIndex]) return
        currentCode = showList[currentIndex].code;

        this.setState({
          searchList_index: currentIndex,
          searchList_active: currentCode,
          searchText: currentCode
        })
        break;
    }
  }

  componentWillMount() {
  }

  componentDidMount() {

  }

  componentWillUpdate() {
  }

  componentDidUpdate() {
    $('#imageBox').viewer()
  }
}


const connectApp = connect(({rejudge3, app}) => {
  return {
    windowHeight: app.windowHeight,
    num_results: rejudge3.num_results,
    surplus: rejudge3.surplus,
    imageInfo: rejudge3.imageInfo,
    sysActive: rejudge3.sysActive,
    rejudgeType: rejudge3.rejudgeType,
    imageCodeInput: rejudge3.imageCodeInput,
    codeBtnActiveNumber: rejudge3.codeBtnActiveNumber,
    hoverCode: rejudge3.hoverCode,
    imageMapLength: rejudge3.imageMapLength,
    imageCurrentIndex: rejudge3.imageCurrentIndex,
    imageCodeList: rejudge3.imageCodeList,
    importCodeState: rejudge3.importCodeState,
    detailFullScrollState: rejudge3.detailFullScrollState,
  }
})(DetailPage)

export default withRouter(connectApp)
